import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { OrderService } from "app/modules/order/service/order.service";
import { UserService } from "app/modules/user/service/user.service";
import { SweetalertComponent } from "app/shared/sweetalert/sweetalert.component";

@Component({
  selector: "app-customer-ledger",
  templateUrl: "./customer-ledger.component.html",
  styleUrls: ["./customer-ledger.component.scss"],
  providers: [SweetalertComponent],
  encapsulation: ViewEncapsulation.None,
})
export class CustomerLedgerComponent implements OnInit {
  public contentHeader: object;
  public ledgerFrom: FormGroup;
  public submitted = false;
  public userDetail;
  public customerData = {
    customerName: null,
    customerNumber: null,
    customerEmail: "",
    customerCity: "",
    customerAddress: "",
  };
  public customerTableData = [];
  public paymentCM = {
    totalAmount: null,
    balanceAmount: null,
    collectedAmount: null,
    receivedAmount: null,
    collectedNow:null,
    advanceAmount:null
  };
  public receivedAmount;
  public balanceAmount;
  public collectedAmount;
  public advancePayment = 0
  constructor(
    private _customerService: UserService,
    private _orderService: OrderService,
    private fb: FormBuilder,
    private _sw: SweetalertComponent,

  ) {}

  onSubmit(formValue) {
    if (
      this.paymentCM.collectedAmount == null ||
      this.paymentCM.collectedAmount == 0
    ) {
      return false;
    }

    this._orderService.updateCustomerLedger(formValue).subscribe((res) => {
      this._sw.fire("success", "Ledger Amount", `Record Successfully Update..`);
      this.submitted = false;
    },
    (err) => {
      this._sw.fire("error", "Ledger Amount", err.error.message);
    });
  }

  selectCustomer(selectedCustomer) {
    this.customerData.customerName = selectedCustomer.full_name;
    this.customerData.customerNumber = selectedCustomer.phone;
    this.customerData.customerEmail = selectedCustomer.email;
    this.customerData.customerCity = selectedCustomer.city;
    this.customerData.customerAddress = selectedCustomer.address;
    this.getLedgerByCustomer({
      customer_name: this.customerData.customerName,
      customer_phone: this.customerData.customerNumber,
    });
  }

  getLedgerByCustomer(getOj) {
    this._orderService.getByCustomer(getOj).subscribe((res) => {
      if (res.data) {
        this.customerTableData = res.data.results;
        this.ledgerFrom.patchValue({
          id: res.data.amount.id,
          totalAmount: res.data.amount.total_amount,
          totalReceived: res.data.amount.total_received,
          totalBalance: res.data.amount.total_balance,
          collectedAmount: res.data.amount.collected_amount,
          advancePayment:res.data.amount.advance_payment
        });
        this.receivedAmount = res.data.amount.total_received;
        this.balanceAmount = res.data.amount.total_balance;
        this.collectedAmount = res.data.amount.collected_amount;
        this.advancePayment = res.data.amount.advance_payment;
      }else{
        this.customerTableData = [];
        this.ledgerFrom.reset()
        this.receivedAmount = 0
        this.balanceAmount = 0
      }
    });
  }
  
  paymentCollectedChange(val: number) {
    this.paymentCM.balanceAmount = this.balanceAmount - +val;
    this.paymentCM.receivedAmount = this.receivedAmount + +val;
    this.paymentCM.collectedAmount = this.collectedAmount + +val;
    if(this.paymentCM.balanceAmount < 0){
      this.paymentCM.advanceAmount = this.advancePayment + -this.paymentCM.balanceAmount
      this.paymentCM.receivedAmount = this.paymentCM.totalAmount
      this.paymentCM.balanceAmount = 0
    }
  }

  getUserDetails() {
    this._customerService.getAll().subscribe((res) => {
      if (res.data) {
        this.userDetail = res.data.filter(
          (type) => type.customer_type == "Stitching"
        );
      }
    });
  }

  makeReactiveForm() {
    this.ledgerFrom = this.fb.group({
      id: [],
      totalAmount: ["", [Validators.required]],
      totalReceived: [],
      totalBalance: [],
      collectedAmount: [],
      advancePayment:[],
    });
  }

  ngOnInit(): void {
    this.getUserDetails();
    this.makeReactiveForm();
    this.contentHeader = {
      headerTitle: "Customer Ledger",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Customer",
            isLink: true,
            link: "/customer",
          },
          {
            name: "Ledger",
            isLink: false,
          },
        ],
      },
    };
  }
}
