import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { RouterModule, Routes } from "@angular/router";
import { ContentHeaderModule } from "app/layout/components/content-header/content-header.module";
import { SharedModule } from "app/shared/shared.module";
import { CoreCommonModule } from "@core/common.module";
import { NgSelectModule } from "@ng-select/ng-select";
import { NgxMaskModule } from "ngx-mask";
import { CustomerLedgerComponent } from './components/customer-ledger/customer-ledger.component';
import { ExpensesFormComponent } from './components/expenses-ledger/expenses-ledger.component';
import { SweetalertComponent } from "app/shared/sweetalert/sweetalert.component";
import { Ng2FlatpickrModule } from "ng2-flatpickr";

const routes: Routes = [
  {
    path: "customer-ledger",
    component: CustomerLedgerComponent,
    data: { animation: "customer-ledger" },
  },
  {
    path: "expenses-ledger",
    component: ExpensesFormComponent,
    data: { animation: "expenses-ledger" },
  },
  {
    path: "expenses-ledger/:id",
    component: ExpensesFormComponent,
    data: { animation: "expenses-ledger-id" },
  },
];

@NgModule({
  declarations: [ CustomerLedgerComponent,ExpensesFormComponent],
  imports: [
    Ng2FlatpickrModule,
    CommonModule,
    FormsModule,
    RouterModule.forChild(routes),
    ContentHeaderModule,
    SharedModule,
    ReactiveFormsModule,
    CoreCommonModule,
    NgSelectModule,
    NgxMaskModule.forRoot(),
  ],
  providers:[SweetalertComponent]
})
export class CustomerLedgerModule {}
