import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { ItemMasterService } from "app/modules/item-master/service/itemMaster.service";
import { SweetalertComponent } from "app/shared/sweetalert/sweetalert.component";
import { ExpensesLedgerService } from "../../service/expenses.service";
import { FlatpickrOptions } from "ng2-flatpickr";

@Component({
  selector: "app-expenses-ledger",
  templateUrl: "./expenses-ledger.component.html",
  styleUrls: ["./expenses-ledger.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class ExpensesFormComponent implements OnInit {
  public DateRangeOptions: FlatpickrOptions = {
    altInput: true,
    mode: "range",
  };
  public contentHeader: Object;
  public expensesLedgerForm: FormGroup;
  public tableDataArray = [];
  public businessType = ["Needlework Stiching", "Needlework Fabric Wholesale"];
  public expensesType = [
    "Karigar Salary",
    "Utility Expenses",
    "Conveyance Expenses",
    "Tea Expenses",
    "Meal Expenses",
    "Guest Meal Expenses",
    "Office Expenses",
    "Miscellaneous Expenses",
  ];
  public submitted = false;
  public editable = false;
  public id;

  constructor(
    private _sw: SweetalertComponent,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private _serviceData: ExpensesLedgerService,
    private _router: Router
  ) {}

  onSubmit(formData) {

    this.submitted = true;
    if (this.expensesLedgerForm.invalid) {
      return;
    }

    if (this.editable) {
      this.updateFormDetail(formData);
    } else {
      this.insertFormDetail(formData);
    }
  }

  insertFormDetail(formData) {
    this._serviceData.create(formData).subscribe(
      (res) => {
        this._sw.fire(
          "success",
          "Expenses Ledger",
          `Record Successfully Add..`
        );
        this.submitted = false;
        this.expensesLedgerForm.reset();
        this.getAllDetail();
      },
      (err) => {
        this._sw.fire("error", "Expenses Ledger", err.error.message);
      }
    );
  }

  updateFormDetail(formData) {
    this._serviceData.update({ id: this.id, ...formData }).subscribe(
      (res) => {
        this._sw.fire(
          "success",
          "Expenses Ledger",
          `Record Successfully Update..`
        );
        this.editable = false;
        this.submitted = false;
        this._router.navigate(["/ledger/expenses-ledger"]);
      },
      (err) => {
        this._sw.fire("error", "Expenses Ledger", err.error.message);
      }
    );
  }

  ngOnInit(): void {
    this.getAllDetail();
    this.id = +this.route.snapshot.paramMap.get("id");
    if (this.id > 0) {
      this.getDetailById();
    }
    this.makeReactiveForm();

    this.contentHeader = {
      headerTitle: "Expenses Ledger",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Expenses",
            isLink: true,
            link: "/",
          },
          {
            name: "Expenses Ledger",
            isLink: false,
          },
        ],
      },
    };
  }

  getDetailById() {
    this._serviceData.getDetailById({ id: this.id }).subscribe((res) => {
      this.editable = true;
      this.expensesLedgerForm.patchValue({
        fromDate: res.data.fromDate,
        toDate: res.data.toDate,
        businessType: res.data.businessType,
        expensesType: res.data.expensesType,
        amount: res.data.amount,
        remarks: res.data.remarks,
      });
    });
  }

  get f() {
    return this.expensesLedgerForm.controls;
  }

  makeReactiveForm() {
    this.expensesLedgerForm = this.fb.group({
      fromDate: ["", [Validators.required]],
      toDate: ["", [Validators.required]],
      businessType: [null, [Validators.required]],
      expensesType: [null, [Validators.required]],
      amount: ["", [Validators.required]],
      remarks: [""],
    });
  }

  getAllDetail() {
    this._serviceData.getAll().subscribe((res) => {
      if (res.data) {
        this.tableDataArray = res.data;
      } else if (!res.data && this.tableDataArray.length == 1) {
        this.tableDataArray = [];
      }
    });
  }

  confirmDelete(id) {
    let deleteId = {
      id: id,
    };
    this._sw.delete(() =>
      this._serviceData.delete(deleteId).subscribe(
        (res) => {
          setTimeout(() => this.getAllDetail(), 2000);
        },
        (err) => {
          this._sw.fire("error", "Collection", "There is a problem");
        }
      )
    );
  }

  
  filterStartDate;
  filterEndDate;
  filterDataByDateRange(value) {
    if(value == ""){
      this.filterStartDate = null
      this.filterEndDate = null
    }

    let dateRange = value.split("to");
    if (dateRange[1]) {
      this.filterStartDate = new Date(dateRange[0]);
      this.filterEndDate = new Date(dateRange[1]);
    }
  }

  get filterTableData() {
    if (!this.filterStartDate) {
      return this.tableDataArray;
    }
    return this.tableDataArray.filter((item) =>
    new Date(item.toDate) >= this.filterStartDate && new Date(item.fromDate) <= this.filterEndDate
    );
  }
}
